import React, { Component } from 'react';
import About from './pages/about';
import style from './index.module.css';
import Switch from './components/switch';
import Projects from './pages/projects';

class App extends Component {

    state = {
      options:[{text:'About', color:'#60FFD9'}, {text:'Projects', color:'#A6EA86'}],
      page:1,
    }

    change_page = page => {
        this.setState({page})
    }


    copy_link = () => {
        const copy_text = this.return_link_to_show()
        navigator.clipboard.writeText(copy_text)
        alert(copy_text + " has been copied to clipboard!")
    }

    icons_container = (mobile) => {
      return (
        <div className={style.icon_container}>
            <img className={mobile?style.icons:style.img} onClick={()=>mobile ? null : this.copy_link()} src={require('./icons/location.png')}/>  
            <img className={mobile?style.icons:style.img} onClick={()=>mobile ? null : this.copy_link()} src={require('./icons/linked_in.png')}/>  
            <img className={mobile?style.icons:style.img} onClick={()=>mobile ? null : this.copy_link()} src={require('./icons/github.png')}/>  
        </div>
      )
    }
    

    render(){

      const { options, page } = this.state;
      const mobile  = navigator.userAgent.indexOf("Mobi") > -1
      

        return(
          <div>
            <header >

                <div className={mobile? style.header_all_m:style.header_all}>  
                
                  <div style={{background:'red'}}>
                    <Switch change={this.change_page} options={options} value={page}/>
                  </div>

                  <div className={mobile? style.header_title_mobile:style.header_title}>
                    <h4>Momodou Bah</h4>
                    <div>{this.icons_container(mobile)}</div>
                  </div> 

                </div>

              </header> 

              <main className={mobile?style.mobile_main:null}>
                {
                  page ? <Projects mobile={mobile}/> : <About mobile={mobile}/>
                }
              </main>


          </div>
        )
    }
}

export default App;
